import { get, post, del, put } from './network'

export const alarmSetting = data => post('/alarm',data)
export const alarmItem = () => get('/alarm')
export const timeData = () => get('/time_series')

// 数据分析模块
export const monitorData = data => get('/analysis/data',data)  // 请求设置条件下的时序数据

// 配置模块
export const ruleSetting = data => post('/setting/rule',data) // 上传告警规则配置
export const ruleGetting = () => get('/setting/rule')         // 获取所有历史规则配置

export const hostNotGetting = () => get('/setting/host/manage')      // 获取没有配置信息的主机id
export const hostSetting = data => post('/setting/host',data)             // 配置主机信息
export const hostAll = () => get('/setting/host')                    // 获取所有主机信息
