import Vue from 'vue'
import Vuex from 'vuex'
import { hostAll, monitorData } from '../request/api'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    series_data: [
      [1645538940000, 4.48],
      [1645539000000, 2.33],
      [1645539060000, 1.73],
      [1645539120000, 4.29],
      [1645539180000, 5.63],
    ],
    hostData: [],
    hostUniData:{},
    nowID:''
  },
  getters: {

  },
  mutations: {
    changeData(state, series_data) {
      state.series_data = series_data
    },
    getHost(state, data) {
      state.hostData = data
    },
    selectChange(state,id){
      for(let i=0;i<state.hostData.length;i++){
        if(state.hostData[i].HostID == id){
          state.hostUniData = state.hostData[i]
        }
      }
    },
    initInfo(state,data){
      state.hostUniData.HostID = data.host_id
      state.hostUniData.Location = data.room
      // console.log(data)
    },
    initLineData(state,data){
      state.series_data = data
    }
  },

  actions: {
    changeCom(context, series_data) {
      context.commit('changeData', series_data)
    },
    async getHostCom(context,data) {
      context.commit('getHost', data)
    },
    async initLineDataCom(context) {
      let params = {
        HostID: this.state.hostID[0],
        StartFrom: '1h',
        MetricsType: 'cpu_rate',
        Granularity: '1m',
        AggregateFunc: 'mean'
      }
      let res = await monitorData(params)
      let { metrics_value, time_stamp } = res.data
      let data = []
      for (let i = 0; i < metrics_value.length; i++) {
        data.push([time_stamp[i] * 1000, metrics_value[i].toFixed(2)])
      }
      context.commit('initLineData',data)
    }
  },
  modules: {
  }
})
