import axios from 'axios'
import router from '../router'

// axios.defaults.baseURL = process.env.VUE_APP_BASE_URL
axios.defaults.timeout = 5000

// axios.interceptors.request.use(config => {
//   config.headers.Authorization = sessionStorage.getItem('token')
//   return config
// }, error => {
//   return Promise.error(error)
// })

axios.interceptors.response.use(res => {
  return Promise.resolve(res)
}, error => {
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        error.message = '请求错误';
        break
      case 401: {
        // 请求方法都带token，若验证失败则直接返回Login页面
        error.message = 'token验证失败'
        router.replace({
          path: '/login'
        })
        break
      }
      case 403:
        error.message = '没有权限,拒绝访问';
        break
      case 404:
        error.message = `请求地址出错`;
        break
      case 408:
        error.message = '请求超时';
        break
      case 500:
        error.message = '服务器内部错误';
        break
      case 501:
        error.message = '服务未实现';
        break
      case 502:
        error.message = '网关错误';
        break
      case 503:
        error.message = '服务不可用';
        break
      case 504:
        error.message = '网关超时';
        break
      case 505:
        error.message = 'HTTP版本不受支持';
        break
      default:
        break
    }
  }
  return Promise.reject(error)
})
export function get(url, params) {
  return new Promise(function (resolve, reject) {
    axios.get(url, {
      params: params
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function post(url, data) {
  return new Promise(function (resolve, reject) {
    axios.post(url, data)
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function del(url, data) {
  return new Promise(function (resolve, reject) {
    axios.delete(url, {
      data: data
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function put(url, data) {
  return new Promise(function (resolve, reject) {
    axios.put(url, {
      data: data
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}